import React from "react";
import { Check, Share2, ArrowRightIcon, CopyIcon } from "lucide-react";
import Link from "next/link";
import Image from "next/image";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "../ui/table";
import PriceFormatter from "../format-price/PriceFormatter";
import { copyToClipboard } from "@/utils";
import OrderListIcon from "@/assets/icons/OrderListIcon";

export type OrderProduct = {
  productId: string;
  count: number;
  price: number;
  product: {
    id: string;
    title: string;
    slug: string;
    productCode: string;
    mainImage: string;
  };
};

export type OrderResponse = {
  id: string;
  orderNumber: string;
  contrAgent: {
    name: string;
    inn: string;
  };
  receiverInfo: {
    fullname: string;
    phone: string;
    email: string;
  };
  deliveryInfo: {
    address: string;
    costLabel: string;
  };
  products: OrderProduct[];
};

interface OrderFinishProps {
  orders: OrderResponse[];
}

const OrderFinish: React.FC<OrderFinishProps> = ({ orders }) => {
  return (
    <div>
      {orders.map((order, idx) => (
        <div key={order.id} className="bg-white border shadow-sectionShadow py-6 px-5 mb-6">
          <div className="mb-4 text-center">
            <h2 className="text-2xl font-normal leading-snug text-textColor">
              Спасибо за Ваш заказ!
            </h2>
          </div>

          {/* Контактная информация */}
          <div className="flex items-center gap-3 pb-4">
            <h3 className="font-normal text-stoneCold text-base">
              1. Контактная информация
            </h3>
            <div className="bg-greenLight w-5 h-5 rounded-full flex items-center justify-center">
              <Check className="text-white" size={10} strokeWidth={3} />
            </div>
          </div>

          <div className="grid grid-cols-2 border border-superSilver bg-final p-4">
            <div className="flex flex-col gap-2">
              <span className="text-weekColor text-xs">Контрагент</span>
              <p className="text-stoneCold text-sm">{order.contrAgent.name} ИНН {order.contrAgent.inn}</p>
            </div>
            <div className="flex flex-col gap-2">
              <span className="text-weekColor text-xs">Телефон</span>
              <p className="text-stoneCold text-sm">{order.receiverInfo.phone}</p>
            </div>
            <div className="flex flex-col gap-2">
              <span className="text-weekColor text-xs">Получатель</span>
              <p className="text-stoneCold text-sm">{order.receiverInfo.fullname}</p>
            </div>
            <div className="flex flex-col gap-2">
              <span className="text-weekColor text-xs">E-mail</span>
              <p className="text-stoneCold text-sm">{order.receiverInfo.email}</p>
            </div>
          </div>

          {/* Информация о доставке */}
          <div className="flex items-center gap-3 pt-6 pb-4">
            <h3 className="font-normal text-stoneCold text-base">
              2. Информация о доставке
            </h3>
            <div className="bg-greenLight w-5 h-5 rounded-full flex items-center justify-center">
              <Check className="text-white" size={10} strokeWidth={3} />
            </div>
          </div>

          <div className="border border-superSilver bg-final1 p-4">
            <div className="grid grid-cols-2 gap-4">
              <div className="flex flex-col gap-2">
                <span className="text-weekColor text-xs">Адрес ПВЗ</span>
                <Link href="#" className="text-stoneCold text-sm">{order.deliveryInfo.address}</Link>
              </div>
              <div className="flex flex-col gap-2">
                <span className="text-weekColor text-xs">Стоимость</span>
                <p className="text-stoneCold text-sm">{order.deliveryInfo.costLabel}</p>
              </div>
            </div>
          </div>

          {/* Список товаров */}
          <div className="pt-6">
            <div className="border-b border-superSilver text-center mb-4">
              <h3 className="relative text-textColor font-normal inline-block text-lg pb-2">
                {idx + 1}. Заказ № {order.orderNumber} (НДС не облагается)
                <span className="absolute bottom-0 left-0 w-full h-1 bg-gradient-to-r from-cerulean to-blue-500"></span>
              </h3>
            </div>

            <Table className="border border-gray-300 rounded-none overflow-hidden w-full">
              <TableHeader>
                <TableRow className="bg-gray-100">
                  <TableHead className="p-3 text-left text-sm font-semibold">Наименование товара</TableHead>
                  <TableHead className="p-3 text-left text-sm font-semibold">Артикул</TableHead>
                  <TableHead className="p-3 text-center text-sm font-semibold">Кол-во</TableHead>
                  <TableHead className="p-3 text-end text-sm font-semibold">Цена</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {order.products.map((product) => (
                  <TableRow key={product.productId}>
                    <TableCell className="p-2 border-r">
                      <div className="flex items-center gap-2">
                        <Image
                          src={product.product.mainImage}
                          alt={product.product.title}
                          width={60}
                          height={60}
                          className="p-1 aspect-square"
                        />
                        <p className="text-sm text-stoneCold font-normal">{product.product.title}</p>
                      </div>
                    </TableCell>
                    <TableCell className="p-2 border-r text-sm text-textColor flex items-center justify-between">
                      {product.product.productCode}
                      <span
                        className="cursor-pointer text-explosiveGrey hover:text-cerulean"
                        onClick={() => copyToClipboard(
                          product.product.productCode,
                          "Артикул скопирован"
                        )}
                      >
                        <CopyIcon />
                      </span>
                    </TableCell>
                    <TableCell className="p-2 border-r text-center text-sm font-normal text-textColor">
                      {product.count}
                    </TableCell>
                    <TableCell className="p-2 text-end text-sm font-normal text-textColor">
                      <PriceFormatter amount={product.price} />
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>

            <div className="border border-gray-300 mt-2">
              <div className="flex justify-between p-3 border-b border-gray-300">
                <p className="text-sm font-semibold text-textColor">В т.ч. доставка</p>
                <p className="text-sm font-semibold text-textColor">будет рассчитана менеджером</p>
              </div>
              <div className="flex justify-between p-3">
                <p className="text-sm font-semibold text-textColor">Сумма</p>
                <p className="text-sm font-semibold text-textColor">
                  <PriceFormatter amount={order.products.reduce((sum, p) => sum + p.price * p.count, 0)} />
                </p>
              </div>
            </div>

            <div className="flex items-center gap-3 my-4">
              <button className="bg-white hover:opacity-90 py-2 px-6 font-semibold text-cerulean border border-cerulean flex items-center gap-2">
                <ArrowRightIcon /> Перейти к заказу
              </button>
              <button className="bg-white hover:opacity-90 p-2 font-semibold text-cerulean border border-cerulean flex items-center">
                <Share2 className="w-5 h-5" />
              </button>
            </div>
          </div>
        </div>
      ))}

      {/* Блок "Остались вопросы" */}
      <div className="bg-white border shadow-sectionShadow py-6 px-5 text-center">
        <h3 className="text-2xl font-normal text-textColor mb-4">Остались вопросы?</h3>
        <p className="text-sm text-textColor mb-2">
          По телефону <Link href="#" className="text-cerulean font-semibold">+998 55 508 0660</Link>
          или <Link href="#" className="text-cerulean font-semibold">@NagSalesBot</Link>
        </p>
        <p className="text-sm text-textColor mb-4">
          Менеджер свяжется с вами, чтобы уточнить стоимость,
          состав и детали заказа и подтвердить его.
        </p>
        <p className="text-sm text-textColor flex items-center justify-center gap-2">
          <OrderListIcon className="w-6 h-6" />
          Счёт после формирования заказа будет выгружен в личный кабинет!
        </p>
      </div>
    </div>
  );
};

export default OrderFinish;
